import VectorLayer from 'ol/layer/Vector.js'
import VectorSource from 'ol/source/Vector.js'
import GeoJSON from 'ol/format/GeoJSON.js'
import Feature from 'ol/Feature.js'
import Point from 'ol/geom/Point.js'
import Overlay from 'ol/Overlay.js'
import { iconStyle, styleFunctionKm } from './map_styling'

function createVectorLayerForJsonFile(json_data, styling, lyrname) {
  const geojsonObject = json_data

  const vectorLayer = new VectorLayer({
    name: lyrname,
    source: new VectorSource({
      features: new GeoJSON().readFeatures(geojsonObject),
    }),
    background: 'transparent',
    style: styling,
  })

  return vectorLayer
}

function updatePositionInMap(coordinates) {
  // Define the point coordinates (longitude, latitude)
  var coordinates = coordinates; // Example: Longitude 10, Latitude 45

  // Create a point feature
  var point = new Feature({
      geometry: new Point(coordinates)
  });

    const vectorLayerCurrentKm = new VectorLayer({
      source: new VectorSource({
        features: [point]
      }),
      style: styleFunctionKm,
    })

  point.setStyle(iconStyle);

  return vectorLayerCurrentKm
}

function updateEtappNrInInfoDiv(nr){
  const etappDiv = document.getElementById('etappNrDiv')
  etappDiv.textContent = nr + " av 13"
  
}

function createOverlay(container) {
const overlay = new Overlay({
  element: container,
  autoPan: {
    animation: {
      duration: 250,
    },
  },
})

return overlay
}

export {createVectorLayerForJsonFile, updatePositionInMap, updateEtappNrInInfoDiv, createOverlay}