let strava_activities_json

//Create a dictionary where each key is a day and the value is a list of activities
let activity_dict = {}
let activity_distinct_days = []

var totalDistanceKm = 0
let listOfActivites = []

var start_date_for_challange = '2025-12-01'

async function getData() {
  const url =
    'https://services-eu1.arcgis.com/3UOELksVE9tSHPLL/ArcGIS/rest/services/tdtd_activities_view/FeatureServer/0/query?where=1%3D1&objectIds=&resultType=none&outFields=&returnIdsOnly=false&returnUniqueIdsOnly=false&returnCountOnly=false&returnDistinctValues=false&cacheHint=false&collation=&orderByFields=&groupByFieldsForStatistics=&returnAggIds=false&outStatistics=&having=&resultOffset=&resultRecordCount=&sqlFormat=none&f=pjson&token='

  try {
    const response = await fetch(url)
    if (!response.ok) {
      throw new Error(`Response status: ${response.status}`)
    }

    const result = await response.json()
    //console.log(result.features)
    strava_activities_json = result.features
    doThingsWhenDataExists()
  } catch (error) {
    console.error(error.message)
  } //
}

async function doThingsWhenDataExists() {
  strava_activities_json.map((activity) => {
    const incoming_date = activity.attributes.workout_date

    if (new Date(start_date_for_challange) <= new Date(incoming_date)) {
      const elapsedtime_sec = activity.attributes.elapsed_time

      const ms = 8000 / 3600 // km/h to m/s
      const distance_m = ms * elapsedtime_sec //m/s * sec s= v*t

      listOfActivites.push(activity.attributes.sport_type)
      totalDistanceKm += Math.round((distance_m / 1000) * 10) / 10 //1 decimal
      //console.log("TotalDistanceKm= " + totalDistanceKm)
      //console.log(activity.attributes.activity_name +  ", elapsed time: " + elapsedtime_sec  + ", activity: " + activity.attributes.sport_type )
    }
  })

  // //Create a dictionary where each key is a day and the value is a list of activities
  strava_activities_json.map((entry) => {
    let act_day_raw = new Date(entry.attributes.workout_date)
    const day = act_day_raw.toLocaleDateString('sv-SE')
    if (!activity_distinct_days.includes(day) && new Date(start_date_for_challange) <= new Date(act_day_raw)) {
      activity_distinct_days.push(day)
    }
  })

  activity_distinct_days.map((day) => {
    const filtered_acts = strava_activities_json.filter((entry) => {
      const act_day_raw = new Date(entry.attributes.workout_date)
      const act_day = act_day_raw.toLocaleDateString('sv-SE')
      if (day == act_day) {
        return entry
      }
    })

    activity_dict[day] = filtered_acts
  })
  totalDistanceKm =  Math.round((totalDistanceKm * 10)/10)
  //console.log('activity_dict: ' + activity_dict)
}

export { totalDistanceKm, listOfActivites, activity_dict, start_date_for_challange, getData }