function countByProperty(data, attribute) {
  //Incoming data is a dict
  if (data) {
    //Get sum of a specified input attribute
    return data.reduce((acc, item) => {
      const key = item.attributes[attribute]
      acc[key] = (acc[key] || 0) + 1
      return acc
    }, {})
  } else {
    return null
  }
}

function countOccurrences(list) {
  // Count occurrences using reduce
  const sortedListOfValues = []
  const sortedListOfLabels = []

  if (list != undefined) {
    const counts = list.reduce((acc, item) => {
      acc[item] = (acc[item] || 0) + 1
      return acc
    }, {})

    //console.log(counts)
    // Output: { bil: 3, mc: 2, bike: 1 }
    for (const [key, value] of Object.entries(counts)) {
      //console.log(`tapp history: ` + key +`: ${value}`)
      sortedListOfValues.push(value)
      sortedListOfLabels.push(key)
    }
  }
  return [sortedListOfValues, sortedListOfLabels]
}

function sumDistancesByType(data) {
  if (data) {
    return data.reduce((acc, item) => {

      const ms = 8000 / 3600 // km/h to m/s
      const distance_m = ms * item.attributes.elapsed_time //m/s * sec s= v*t

      // Round to 1 decimal place
      const distance_km = Math.round((distance_m / 1000) * 10) / 10

      acc[item.attributes.sport_type] =
        (acc[item.attributes.sport_type] || 0) + distance_km  //Math.round((distance_m / 1000) * 100) / 100
      return acc
    }, {})
  } else {
    return null
  }
}

