function updateProgressBarWithCurrentKmValue(km_value) {
  // Transfer the current KM value to the div's (progress bar + etapp list)

  const totalKm = Math.round(1487442.4 / 1000) //Static value - with correct in total distans for Tour de TD
  const percentage = Math.min((km_value / totalKm) * 100, 100)

  //Update TOTAL length div
  const divTotal = document.getElementById('sumTotalLengthKmDiv')
  divTotal.textContent = km_value

  //Update progress bar
  const progressBar = document.getElementById('progressBar')
  const progressInfo = document.getElementById('progressInfo')

  progressBar.style.width = percentage + '%'
  progressBar.textContent = Math.round(percentage) + '%'
  progressInfo.textContent = `${km_value} km av totalt ${totalKm} km`
}

function findClosestPoint(paths, targetKm) {
  //Find the closest M-value compare to the given Km-value (TD total distans at the monent)
  let closestPoint = null
  let minDiff = Infinity
  let coordinate = null

  // Flatten the nested paths array and search for the closest point
  paths.forEach((path) => {
    path.forEach((point) => {
      if (point.length > 2) {
        const km = point[2]
        const diff = Math.abs(km - targetKm)

        if (diff <= minDiff) {
          minDiff = diff
          closestPoint = point
          coordinate = [point[0], point[1]]
        }
      }
    })
  })

  return [closestPoint, coordinate]
}

function findClosestKmValueByIndex0(array, inputValue) {
  //From the list of closest km/m-value for each polyline segment, find the one we're actually looking for
  let closestItem = null
  let minDiff = Infinity

  array.forEach((item) => {
    if (item[0][2] > 0) {
      const diff = Math.abs(item[0][2] - inputValue)
      if (diff < minDiff) {
        minDiff = diff
        closestItem = item
      }
    }
  })

  return closestItem
}

function getCoordinatesForCurrentKmValue(km_value) {
  //Search for closest M-value in json
  const myObj = json_route_incl_M
  const polyline = myObj.features
  const listOfAlternativs = []
  const listOfAlternativsInclEtapp = []
  let closest_coordinate

  if (km_value != 0) {
    for (const index in polyline) {
      const etapp = polyline[index].attributes.Etapp
      const etapp_name = polyline[index].attributes.Name
      const paths = polyline[index].geometry.paths
      const closest = findClosestPoint(paths, km_value)
      //console.log('Closest point to', km_value, 'km is:', closest[0], '. Use this coordinates: ' + closest[1])
      listOfAlternativs.push(closest)
      listOfAlternativsInclEtapp.push([closest[1], etapp])
    }

    //get the best alternativ - the closest from all results/segments
    closest_coordinate = findClosestKmValueByIndex0(listOfAlternativs, km_value)[1]
    //console.log(closest_coordinate)
    //also get the etapp for that closest km
    listOfAlternativsInclEtapp.map((item) => {
      if (item[0] == closest_coordinate) {
        const etappNr = item[1]
        updateEtappNrInInfoDiv(etappNr)
      }
    })
  } else {
    updateEtappNrInInfoDiv(1)
    closest_coordinate = [1428805.1897, 7645124.6011999995]
  }
  const vectorLayerCurrentKm = updatePositionInMap(closest_coordinate)

  //When closest value is found get the coordinates and create/update the vector point layer
  map.addLayer(vectorLayerCurrentKm)
  layerExtent = vectorLayerCurrentKm.getSource().getExtent()
}

