function calculateActivityTypePerEtapp() {
  // console.log('>>>>>>>>> calculateActivityTypePerEtapp start')
  //Search for closest M-value in json
  const myObj = json_route_incl_M
  const polyline = myObj.features

  const listEtappDividing = []
  const etapp_history_dict = {}
  let sum_distance_since_start = 0

  for (const index in polyline) {
    const etapp = polyline[index].attributes.Etapp
    const etapp_Delstracka_start_km = polyline[index].attributes.Delstracka_start_km
    const etapp_Delstracka_slut_km = polyline[index].attributes.Delstracka_slut_km
    listEtappDividing.push([etapp, etapp_Delstracka_start_km, etapp_Delstracka_slut_km])
  }

  //loop trough all activites in the order the are stored/recorded
  for (const [key, value] of Object.entries(activity_dict)) {
    const key_activity_date = key
    const value_all_activities_by_date_list = value
    //console.log(`${key}: ${value}`)

    value_all_activities_by_date_list.map((specific_activity) => {
      const activity_name = specific_activity.attributes.name
      const activity_elapsedtime = specific_activity.attributes.elapsed_time
      const activity_type = specific_activity.attributes.sport_type

      const ms = 8000 / 3600 // km/h to m/s
      const distance_m = ms * activity_elapsedtime //m/s * sec s= v*t
      const distance_km = Math.round((distance_m / 1000) * 10) / 10 //distance_m/1000
      sum_distance_since_start += distance_km

      listEtappDividing.map((specific_etapp) => {
        const etapp_name = specific_etapp[0]
        const etapp_Delstracka_start_km = specific_etapp[1]
        const etapp_Delstracka_slut_km = specific_etapp[2]

        if (
          sum_distance_since_start > etapp_Delstracka_start_km &&
          sum_distance_since_start < etapp_Delstracka_slut_km
        ) {
          if (!etapp_history_dict[etapp_name]) {
            // If key doesn't exist, create it with a list containing the new value
            etapp_history_dict[etapp_name] = [activity_type]
          } else {
            // If key exists, push the new value into the existing list
            // console.log('etapp name:' + etapp_name)
            etapp_history_dict[etapp_name].push(activity_type)
          }
        }
      })
    })
  }
  // for (const [key, value] of Object.entries(etapp_history_dict)) {
  //   console.log(`Etapp history: ` + key + `: ${value}`)
  // }

  // console.log('>>>>>>>>> calculateActivityTypePerEtapp end')
  return etapp_history_dict
}

